<template>
    <div>
        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="primary" size="small" @click="handleAdd"></el-button>
        </el-col>
        <el-table :data="sortList" ref="sortListTable" v-loading="listLoading"
                  @selection-change="selsChange" style="width: 100%;">
            <el-table-column label="" prop="createAdmin"></el-table-column>
            <el-table-column label="" prop="createTime"></el-table-column>
            <el-table-column label="" prop="name"></el-table-column>
            <el-table-column label="" prop="updateAdmin"></el-table-column>
            <el-table-column label="" prop="updateTime"></el-table-column>
            <el-table-column label="" width="170">
                <template slot-scope="scope">
                    <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false" @close="closeEditDialog('editForm')">
            <el-form :model="editObj" :rules="editFormRules" ref="editForm">
                <el-form-item label="" prop="name" label-width="80px">
                    <el-input v-model="editObj.name" placeholder="" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="handleEditFormSubmit('editForm')"></el-button>
            </div>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false" @close="closeAddDialog('addForm')">
            <el-form :model="addObj" :rules="addFormRules" ref="addForm">
                <el-form-item label="" prop="name" label-width="80px">
                    <el-input v-model="addObj.name" placeholder="" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="handleAddFormSubmit('addForm')"></el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script type="text/ecmascript-6">
    import { ERR_OK0, ERR_OK1, ERR_OK2, SYS_ERROR_MSG } from '@/common/config/config';
    import util from '@/common/js/util';
    export  default {
        data() {
            return {
                // 
                listLoading: false,
                // 
                addFormVisible: false,
                    // 
                addObj: {
                    name: ''
                },
                addFormRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
                // 
                sortList: [],

                // 
                editFormVisible: false,
                editObj: {
                    id: '',
                    name: ''
                },
                // 
                editFormRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
                // 
                sels: []
            };
        },

        methods: {
            // 
            getAllSorts() {
                this.$http.get('/api/admin/good/findAllSorts').then(res => {
                    let code = res.data.code;
                    if(code === ERR_OK0) {
                        let sortList = res.data.data;
                        this.sortList = sortList;
                        let sortGroup = [];
                        for(let i in sortList) {
                            let sortObj = {value: sortList[i].name, label: sortList[i].name};
                            sortObj = JSON.stringify(sortObj);
                            sortGroup.push(sortObj);
                        }
                        sessionStorage.sortGroup = sortGroup;
                    }
                }, err => {
                    alert(SYS_ERROR_MSG + err);
                });
            },

            // 
            selsChange(sels) {
                this.sels = sels;
            },

            // clearValidate
            handleAdd() {
                this.addFormVisible = true;
                util.emptyObj(this.addObj);
            },

            // 
            closeAddDialog(formName){
                this.$refs[formName].clearValidate();
            },

            // 
            handleAddFormSubmit(formName) {
                this.$refs[formName].validate(valid => {
                   if(valid) {
                       this.$confirm('', '', {}).then(() => {
                           this.listLoading = true;
                           let para = this.addObj;

                           this.$http.post('/api/admin/good/addSortItem', para).then(res => {
                               let code = res.data.code;
                               if(code === ERR_OK0) {
                                   alert('');
                                   this.listLoading = false;
                                   this.addFormVisible = false;
                                   this.getAllSorts();
                               }else {
                                   let msg = res.msg;
                                   alert(msg);
                               }
                           }, err => {
                                    alert(SYS_ERROR_MSG + err);
                           });
                       });
                   }else {
                       return false;
                   }
                });
            },

            //  /admin/good/updateSortItem
            // 
            closeEditDialog(formName){
                this.$refs[formName].clearValidate();
            },

            // 
            handleEdit(index, row) {
                this.editObj.id = row.id;
                this.editObj.name = row.name;
                this.editFormVisible = true;
            },

            // 
            handleEditFormSubmit(formName) {
                this.$refs[formName].validate(valid => {
                    if(valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.listLoading = true;
                            let para = this.editObj;
                            this.$http.post('/api/admin/good/updateSortItem', para).then(res => {
                                let code = res.data.code;
                                if(code === ERR_OK0) {
                                    this.editFormVisible = false;
                                    alert('');
                                    this.listLoading = false;
                                    this.getAllSorts();
                                }else {
                                    let msg = res.msg;
                                    alert(msg);
                                }
                            }, err => {
                                alert(SYS_ERROR_MSG + err);
                            });
                        });
                    }else {
                         return false;
                    }
                });
            },

            // 
            handleDel(index, row) {
                this.$confirm('', '', {}).then(() => {
                    this.listLoading = true;
                    let para = {
                        id: row.id
                    };
                    this.$http.post('/api/admin/good/deleteSortItem', para).then(res => {
                        let code = res.data.code;
                        if(code === ERR_OK0) {
                            alert('');
                            this.listLoading = false;
                            this.getAllSorts();
                        }else {
                            let msg = res.msg;
                            alert(msg);
                        }
                    }, err => {

                    });
                });
            }
        },

        mounted() {
            this.getAllSorts();
        }
    };
</script>

<style lang="less">

</style>


