<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :rules="queryRules" :model="queryObj">
                <!--<el-form-item>-->
                    <!--<el-select v-model="queryKeyDefault" placeholder="">-->
                        <!--<el-option-->
                                <!--v-for="item in enterWarehouseQueryKeyGroup"-->
                                <!--:key="item.value"-->
                                <!--:label="item.label"-->
                                <!--:value="item.value">-->
                        <!--</el-option>-->
                    <!--</el-select>-->
                <!--</el-form-item>-->
                <el-form-item prop="name">
                    <el-autocomplete
                            class="inline-input"
                            v-model="queryObj.name"
                            :fetch-suggestions="querySearch"
                            placeholder=""
                            :trigger-on-focus="false"
                            @select="handleNameSelect"
                    ></el-autocomplete>
                </el-form-item>
                <el-form-item>
                    <el-col :span="11">
                        <el-form-item prop="price_start">
                            <el-input v-model="queryObj.price_start" placeholder="" style="width: 100%;"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col class="line" :span="1" style="margin-left: 2px;">-</el-col>
                    <el-col :span="11">
                        <el-form-item prop="price_end">
                            <el-input v-model="queryObj.price_end" placeholder="" style="width: 100%;"></el-input>
                        </el-form-item>
                    </el-col>
                </el-form-item>
                <el-form-item prop="sort">
                    <el-select v-model="queryObj.sort" filterable placeholder="" @change="handleSortSelect">
                        <el-option
                                v-for="item in sortNames"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <!--<el-form-item v-if="datePickerShow">-->
                    <!--<el-date-picker-->
                            <!--unlink-panels-->
                            <!--format="yyyy-MM-dd"-->
                            <!--value-format="yyyy-MM-dd"-->
                            <!--v-model="dateDefault"-->
                            <!--type="daterange"-->
                            <!--range-separator=""-->
                            <!--start-placeholder=""-->
                            <!--end-placeholder="">-->
                    <!--</el-date-picker>-->
                <!--</el-form-item>-->
                <el-form-item>
                    <el-button type="primary" @click="handleQuery"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleAdd"></el-button>
                </el-form-item>
            </el-form>
        </el-col>

        <!---->
        <el-table :data="enterWarehouseRecords" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="selection" width="55" column-key="id" fixed="left">
            </el-table-column>
            <el-table-column type="index" width="60" fixed="left">
            </el-table-column>
            <el-table-column label="" width="600">
                <el-table-column prop="name" label="" width="100" column-key="id"></el-table-column>
                <el-table-column prop="sort" label="" width="100"></el-table-column>
                <el-table-column prop="spec" label="" width="100"></el-table-column>
                <el-table-column prop="unit" label="" width="100"></el-table-column>
                <el-table-column prop="number" label="" width="100" sortable></el-table-column>
                <el-table-column prop="price" label="" width="100" sortable></el-table-column>
            </el-table-column>
            <el-table-column prop="createAdmin" label=" " width="100">
            </el-table-column>
            <el-table-column type="date" prop="createTime" label="" width="160" sortable>
            </el-table-column>
            <el-table-column type="date" prop="updateTime" label="" min-width="160" sortable>
            </el-table-column>
            <el-table-column prop="updateAdmin" label="" min-width="130">
            </el-table-column>
            <el-table-column label="" width="170">
                <template slot-scope="scope">
                    <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" :disabled="this.sels.length === 0" @click="batchRemove"></el-button>
            <el-pagination class="pagenation"
                           @size-change="handleSizeChange"
                           @current-change="handleCurrentChange"
                           :current-page="pagenation.currentPage"
                           :page-sizes="pagenation.pageSizes"
                           :page-size="pagenation.pageSize"
                           layout="total, sizes, prev, pager, next, jumper"
                           :total="pagenation.totalElements">
            </el-pagination>
        </el-col>

        <!---->
        <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
            <el-form :model="editGoodItem" label-width="80px" :rules="editFormRules" ref="editForm">
                <el-form-item label="" prop="name">
                    <el-input v-model="editGoodItem.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="number">
                    <el-input v-model="editGoodItem.number" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="price">
                    <el-input v-model="editGoodItem.price" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="sort">
                    <el-select v-model="editGoodItem.sort" placeholder="">
                        <el-option
                                v-for="item in sortNames"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="spec">
                    <el-input v-model="editGoodItem.spec" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="unit">
                    <el-input v-model="editGoodItem.unit" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button></el-button>
                <el-button type="primary" :loading="editLoading" @click="submitEditForm('editForm')"></el-button>
            </div>
        </el-dialog>

        <!---->
        <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
            <el-form :model="newGoodItem" label-width="80px" :rules="addFormRules" ref="addForm">
                <el-form-item label="" prop="name">
                    <el-input v-model="newGoodItem.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="number">
                    <el-input v-model="newGoodItem.number" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="price">
                    <el-input v-model="newGoodItem.price" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="sort">
                    <el-select v-model="newGoodItem.sort" placeholder="" @change="addFormSortSelected">
                        <el-option
                                v-for="item in sortNames"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" prop="spec">
                    <el-input v-model="newGoodItem.spec" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="unit">
                    <el-input v-model="newGoodItem.unit" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button></el-button>
                <el-button type="primary" :loading="addLoading" @click="submitAddForm('addForm')"></el-button>
            </div>
        </el-dialog>
    </section>
</template>

<script>
    import util from '../../common/js/util'
    //import NProgress from 'nprogress'
    import {getEnterWarehouseRecordListpage } from '../../api/api';
    import { ERR_OK0, ERR_OK1, ERR_OK2 } from '@/common/config/config';
    import { addNewThing } from '@/api/api';
    import axios from 'axios';

    export default {
        data() {
            // 
            var checkTotal = (rule, value, callback) => {
                // if(!value) {
                //     return callback(new Error(''));
                // }
                let  rex = /^[1-9]\d*$/;
                setTimeout(() => {
                    if(!rex.test(value)) {
                        callback("	");
                    }else {
                        callback();
                    }
                }, 1000);
            };

            // 
            var checkUnitPrice = (rule, value, callback) => {
                if(!value) {
                    return callback(new Error(''));
                }
                let  rex = /^[0-9]+(.[0-9]{1,2})?$/;
                setTimeout(() => {
                    if(!rex.test(value)) {
                        callback("");
                    }else {
                        callback();
                    }
                }, 1000);
            };

            var checkPriceQuery = (rule, value, callback) => {
                let  rex = /^[0-9]+(.[0-9]{1,2})?$/;
                console.log(value);
                setTimeout(() => {
                    if(!rex.test(value)) {
                        callback("");
                    }else {
                        callback();
                    }
                }, 1000);
            };

            return {
                // 
                goods:[],
                // 
                queryObj: {
                    page: 0,
                    size: 20,
                    name: '',
                    price_start: '',
                    price_end: '',
                    sort: ''
                }, // post
                // 
                queryRules: {
                    price_start: [
                        { validator: checkPriceQuery, trigger: 'blur' },
                    ],
                    price_end: [
                        { validator: checkPriceQuery, trigger: 'blur' },
                    ]
                },
                //
                newGoodItem: {},
                // 
                editGoodItem: {},
                editFormVisible: false,//
                editLoading: false,
                editFormRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    number: [
                        { validator: checkTotal, trigger: 'blur' }
                    ],
                    price: [
                        { validator: checkUnitPrice, trigger: 'blur' },
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    sort: [
                        {required: true, message: '', trigger: 'change' }
                    ],
                    spec: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    unit: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                },
                sortNames: [],
                queryValueSelected: '',
                // 
                dateShow: false,
                inputShow: false,
                queryKeyDefault: '',
                enterWarehouseQueryKeyGroup: [],
                // 
                enterWarehouseRecords: [],
                pagenation: {
                    totalElements: 0,
                    currentPage: 1,
                    pageSizes: [10, 20, 50, 100],
                    pageSize: 20
                },
                listLoading: false,
                sels: [],//

                // 
                addFormVisible: false,//
                addLoading: false,
                // 
                addFormRules: {
                    name: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    number: [
                        { validator: checkTotal, trigger: 'blur' }
                    ],
                    price: [
                        { validator: checkUnitPrice, trigger: 'blur' },
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    sort: [
                        {required: true, message: '', trigger: 'change' }
                    ],
                    spec: [
                        {required: true, message: '', trigger: 'blur'}
                    ],
                    unit: [
                        {required: true, message: '', trigger: 'blur'}
                    ]
                }
            };
        },
        methods: {
            // selectQueryKey(value) {
            //     if(value === 'name') {
            //         this.sortQueryShow = false;
            //         this.priceQueryShow = false;
            //         this.nameQueryShow = true;
            //     }else if(value === 'all') {
            //         this.nameQueryShow = false;
            //         this.sortQueryShow = false;
            //         this.priceQueryShow = false;
            //     }else if(value === 'sort') {
            //         this.nameQueryShow = false;
            //         this.priceQueryShow = false;
            //         this.sortQueryShow = true;
            //     }else if(value === 'price') {
            //         this.nameQueryShow = false;
            //         this.sortQueryShow = false;
            //         this.priceQueryShow = true;
            //     }else {
            //         this.nameQueryShow = false;
            //         this.sortQueryShow = false;
            //         this.priceQueryShow = false;
            //     }
            //     this.queryValueSelected = value;
            // },

            // input
            querySearch(queryString, cb) {
                let goods = this.goods;
                let results = queryString ? goods.filter(this.createFilter(queryString)) : goods;
                console.log(results);
                //  callback 
                cb(results);
            },
            createFilter(queryString) {
                return (good) => {
                    return (good.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
                };
            },
            // 
            handleNameSelect(item) {
                this.queryObj.name = item.value;
            },
            // 
            handleSortSelect(item) {
                this.queryObj.sort = item;
            },
            // 
            handleQuery(e) {
              e.preventDefault();
              let queryObj = this.queryObj;

              this.$http.post('/api/admin/good/findGoodItemsBySearchParams', queryObj, { emulateJSON: true}).then(res => {
                    let code = res.code;
                    console.log(res);
                    if(code === ERR_OK0) {
                        let goods = res.data.data;
                        this.enterWarehouseRecords = goods;
                        this.pagenation.totalElements = goods.length;
                    }
              }, err => {
                  console.log(err);
              });
            },
            // //
            // formatSex: function (row, column) {
            //     return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
            // },
            // handleCurrentChange(val) {
            //     this.page = val;
            //     this.getUsers();
            // },
            // 
            handleSizeChange(val) {
                this.pagenation.pageSize = val;
                let page = this.pagenation.currentPage;
                let size = this.pagenation.pageSize;
                let queryObj = this.queryObj;
                this.queryObj.size = size;
                let params = { page: page,
                    size: size,
                    name: queryObj.name,
                    price_start: queryObj.price_start,
                    price_end: queryObj.price_end,
                    sort: queryObj.sort
                };
                this.$http.post('/api/admin/good/findGoodItemsBySearchParams', params, { emulateJSON: true}).then(res => {
                    let code = res.code;
                    if(code === ERR_OK0) {
                        this.enterWarehouseRecords = res.data.data;
                    }else {
                        let msg = res.msg;
                        alert(msg);
                    }
                }, err => {
                    alert('  '  + err);
                });
            },

            handleCurrentChange(val) {
                let page = val;
                let size = this.pagenation.pageSize;
                let queryObj = this.queryObj;
                this.queryObj.page = page;
                let params = { page: page,
                    size: size,
                    name: queryObj.name,
                    price_start: queryObj.price_start,
                    price_end: queryObj.price_end,
                    sort: queryObj.sort
                };
                this.$http.post('/api/admin/good/findGoodItemsBySearchParams', params, { emulateJSON: true}).then(res => {
                    let code = res.code;
                    if(ERR_OK0 === code) {
                        this.enterWarehouseRecords = res.data.data;
                    }else {
                        let msg = res.msg;
                        alert(msg);
                    }
                }, err => {
                        alert('  ' + err);
                });
            },

            //
            handleAdd() {
                this.addFormVisible = true;
                this.addForm = {
                    name: '',
                    number: 0,
                    price: 0,
                    sort: '',
                    spec: '',
                    unit: ''
                };
            },

            addFormSortSelected(item) {
                this.newGoodItem.sort =item;
            },

            // 
            submitAddForm(formName) {
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.addLoading = true;
                            let para = this.newGoodItem;
                            this.$http.post('/api/admin/good/addGoodItem', para).then(res => {
                                let code = res.code;
                                if(code === ERR_OK0) {
                                    alert("");
                                    history.go(0);
                                }else {
                                    alert(res.msg);
                                }
                            }, err => {
                                alert('');
                            });
                        });
                    } else {
                        return false;
                    }
                });
            },

            //
            handleEdit: function (index, row) {
                this.editFormVisible = true;
                this.editGoodItem = Object.assign({}, row);
            },

            //
            submitEditForm: function () {
                this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            this.editLoading = true;
                            //NProgress.start();
                            let para = {};
                            para.id = this.editGoodItem.id;
                            para.name = this.editGoodItem.name;
                            para.sort = this.editGoodItem.sort;
                            para.unit = this.editGoodItem.unit;
                            para.price = this.editGoodItem.price;
                            para.spec = this.editGoodItem.spec;
                            para.number = this.editGoodItem.number;
                            this.$http.post('/api/admin/good/updateGoodItem',para).then(res => {
                                let code = res.code;
                                if(ERR_OK0 === code) {
                                    alert('');
                                    setTimeout(() => {
                                        history.go(0);
                                    }, 200);
                                }else {
                                    let msg = res.msg;
                                    alert(msg);
                                }
                            }, err => {
                                alert('   ' + err);
                            });
                        });
                    }
                });
            },

            // 
            handleDel: function (index, row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    let para = { id: row.id };
                    this.$http.post('/api/admin/good/deleteGoodItem',para).then(res => {
                        let code = res.code;
                        if(ERR_OK0 === code) {
                            alert('');
                            setTimeout(() => {
                                history.go(0);
                            }, 200);
                        }else {
                            let msg = res.msg;
                            alert(msg);
                        }
                    }, err => {
                        alert('   ' + err);
                    });
                    // removeUser(para).then((res) => {
                    //     this.listLoading = false;
                    //     //NProgress.done();
                    //     this.$message({
                    //         message: '',
                    //         type: 'success'
                    //     });
                    //     this.getUsers();
                    // });
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                let para = this.sels.map(item => {
                    return {id: item.id};
                });
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    this.$http.post('/api/admin/good/deleteGoodItems', para).then(res => {
                        this.listLoading = false;
                        let code = res.code;
                        if(code === ERR_OK0) {
                            alert('');
                            history.go(0);
                        }else {
                            let msg = res.msg;
                            alert(msg);
                        }
                    }, err => {
                        alert('   ' + err);
                    });
                });
            }
        },
        mounted() {
            let tableId = 'enterWarehouseRecords';
            let currentPage = 1;
            let pageSize = 20;
            let queryKey = this.queryKey;
            let params = {
                page: 0
            };

            // /admin/config/getInitData
            let sortGroup = '[' + sessionStorage.sortGroup + ']';
            sortGroup = JSON.parse(sortGroup);
            this.sortNames = sortGroup;
            console.log(this.sortNames)

            // 
            this.queryObj.page = 0;
            this.queryObj.size = 20;
            //  /admin/good/findAllGoodItems
            this.$http.get('/api/admin/good/findAllGoodItems?page=0').then(res => {
                let code = res.code;
                let totalElements = res.data.data.totalElements;
                this.pagenation.totalElements = totalElements;
                if(code === ERR_OK0) {
                    let goods = res.data.data.content;
                    this.enterWarehouseRecords = goods;
                }
                if(res.code === 0) {
                    let enterWarehouseRecords = res.data;

                }
            }, err => {
                console.log(err);
            });

            // 
            this.enterWarehouseQueryKeyGroup = [
                {
                    value: 'name',
                    label: ''
                },
                {
                    value: 'price',
                    label: ''
                },
                {
                    value: 'sort',
                    label: ''
                }
            ];
            // getEnterWarehouseRecordListpage(params).then(res => {
            //     console.log(res.data);
            //     //this.enterWarehouseQueryKeyGroup = res.data.enterWarehouseKey;
            //     //this.queryKeyDefault = res.data.enterWarehouseKey[0].value;
            //     //let total = res.data.total;
            //     let enterWarehouseRecords = res.data;
            //     this.enterWarehouseRecords = enterWarehouseRecords;
            //     //this.pagenation.total = total;
            // }).catch(function(err){
            //     console.log(err);
            // });
        }
    }

</script>

<style scoped>
    .pagenation {
        float: right;
    }
</style>


